import ReactMarkdown from "react-markdown";
import SyntaxHighlighter from "react-syntax-highlighter";
import { docco, github } from "react-syntax-highlighter/dist/esm/styles/hljs";

// const remarkable = new Remarkable();

const SyntaxHighlightComponent = ({ children, language }) => {
  // console.log("MyComponent", children, language);
  // const codeString = "(num) => num + 1";
  language = language ? language : 'javascript';
  return (
    <SyntaxHighlighter
      language={language ?? null}
      style={github}
      showLineNumbers={true}
      // showInlineLineNumbers={false}
      // PreTag="div"
      className="codeStyle"
      wrapLines={true}
    >
      {children ?? ""}
    </SyntaxHighlighter>

    // <SyntaxHighlighter
    // style={docco}
    // language={language}
    // children={value}
    // />

    // <div className='empty-viewer'>Type in Editor to render HTML</div>
  );
};

